import { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import Speech from 'react-speech';
import { getRandomColor, shuffle } from '../helper/functionality';
import { getRandomQuestion } from '../services/Questions';
import { ReactComponent as Back } from '../assets/back-solid.svg';
import { ReactComponent as Home } from '../assets/home-solid.svg';
import { ReactComponent as Forward } from '../assets/forward-solid.svg';
import '../styles/Questions.css';

function Question({ onEmotionChange, onScoreChange }) {
    let isClicked = true;
    const history = useRef({ undo: [], redo: [] });
    const [question, setQuestion] = useState(getRandomQuestion());
    const [order, setOrder] = useState({
        question: question.qList[question.q],
        words: question.qList[question.q].en.split(' '),
        options: shuffle(question.qList[question.q].en.split(' ')),
        answer: [], selection: 0
    });

    useEffect(() => {
        const rsPlay = document.getElementsByClassName('rs-play');
        for (const element of rsPlay) {
            const color = getRandomColor();
            const style = `background-color:${color}33;border-color:${color}`;
            element.addEventListener('mouseleave', () => { element.style = style; });
            element.style = style;
        }
    }, [question]);

    const setCurrentQuestion = (current) => {
        if (current.type === 'order') {
            setOrder({
                question: current.qList[current.q],
                words: current.qList[current.q].en.split(' '),
                options: shuffle(current.qList[current.q].en.split(' ')),
                answer: [], selection: 0
            });
        }
        setQuestion(current);
    }

    const handleClick = (value) => {
        onScoreChange.current(value);
        onEmotionChange.current(value);
        if (value) {
            setTimeout(() => { onEmotionChange.current(); }, 700);
            if (isClicked) {
                isClicked = false;
                history.current.redo = [];
                if (history.current.undo.length > 50) {
                    history.current.undo.shift();
                }
                history.current.undo.push(question);
                setTimeout(() => {
                    const newQuestion = getRandomQuestion();
                    setCurrentQuestion(newQuestion);
                }, 1000);
            }
        }
    }

    const handleUndo = () => {
        if (history.current.undo.length > 0) {
            history.current.redo.push(question);
            setCurrentQuestion(history.current.undo.pop());
        }
    }

    const handleRedo = () => {
        if (history.current.redo.length > 0) {
            if (history.current.undo.length > 50) {
                history.current.undo.shift();
            }
            history.current.undo.push(question);
            setCurrentQuestion(history.current.redo.pop());
        }
    }

    const handleDrop = (i) => {
        if (i < 0) {
            order.options.push(order.answer.splice(order.drag, 1, null)[0]);
            if (order.selection < 0) { order.selection = order.drag; }
        } else if (order.from === 'a') {
            if (order.answer[i]) {
                let word = order.answer[i];
                order.answer[i] = order.answer[order.drag];
                order.answer[order.drag] = word;
            } else {
                order.answer.splice(i, 1, order.answer.splice(order.drag, 1, null)[0]);
            }
        } else {
            moveWord(order.drag, i);
        }
        setOrder({ ...order });
    }

    const handleSelect = (i) => {
        if (order.options.length < 1) {
            order.options.push(order.answer[i]);
            order.answer[i] = null;
        }
        setOrder({ ...order, selection: i });
    }

    const handleOrderClick = (i) => {
        moveWord(i, order.selection);
        if (order.selection >= order.words.length - 1) {
            order.selection = order.answer.findIndex(i => i == null);
        } else {
            order.selection += 1;
        }
        setOrder({ ...order });
    }

    const moveWord = (from, to) => {
        if (order.answer[to]) {
            let word = order.answer[to];
            order.answer[to] = order.options[from];
            order.options[from] = word;
        } else {
            order.answer[to] = order.options.splice(from, 1)[0];
        }
    }

    return (
        <div className="question-detail">
            <div>
                <div>
                    <button className="el-btn" onClick={handleUndo}><Back /></button>
                    <Link to="/" className="el-btn"><Home /></Link>
                    <button className="el-btn" onClick={handleRedo}><Forward /></button>
                    <h2>{question.qList[question.q].tr}</h2>
                    {question.qList[question.q].quotes && <p className="question-quotes"><em>{question.qList[question.q].quotes}</em></p>}
                    <p><span className="question-type">{question.qList[question.q].type}</span> Question: Choose correct {question.qList[question.q].type}.</p>
                </div>
                {question.qList[question.q].img && <img src={question.qList[question.q].img} alt={question.qList[question.q].tr} />}
            </div>
            {question.type !== 'order' ? question.qList.map((item, itemI) => {
                return (
                    <div key={itemI} className="el-option" onClick={() => { handleClick(itemI === question.q); }}>
                        <Speech textAsButton displayText={item.en} voice="Google UK English Female" text={item.en} />
                    </div>
                );
            }) : <>
                <div className="order-question">
                    {order.words.map((item, itemI) => {
                        return (
                            <span key={'a' + itemI} className="el-blank" style={{ backgroundColor: order.selection === itemI && '#80000033', textTransform: itemI === 0 && 'capitalize' }}
                                onClick={() => handleSelect(itemI)} onDoubleClick={order.answer[itemI] ? () => { order.drag = itemI; handleDrop(-1); } : undefined}
                                onDragOver={e => e.preventDefault()} onDrop={() => handleDrop(itemI)} onDragStart={() => { order.drag = itemI; order.from = 'a'; }}
                                draggable={order.answer[itemI] ? true : undefined}>
                                {order.answer[itemI] ?
                                    order.answer[itemI].toLowerCase().replace(/[.,/#!?$%^&*;:{}=\-_`~()]/g, '') :
                                    item.replace('.', '').replaceAll(/./g, '_')
                                }
                            </span>
                        );
                    })}
                </div>
                <div className="order-question" onDragOver={e => { if (order.from === 'a') e.preventDefault(); }} onDrop={() => handleDrop(-1)}>
                    {order.options.map((item, itemI) => {
                        return (
                            <button key={'o' + itemI} className="el-btn" onClick={() => handleOrderClick(itemI)}
                                onDragStart={() => { order.drag = itemI; order.from = 'o'; }} draggable>
                                {item.toLowerCase().replace(/[.,/#!?$%^&*;:{}=\-_`~()]/g, '')}
                            </button>
                        );
                    })}
                </div>
                <div className="el-option" style={order.options.length > 0 ? { visibility: 'hidden' } : undefined} onClick={() => { handleClick(order.answer.join(' ') === order.question.en); }}>
                    <Speech textAsButton styles={CheckButton} displayText="Check" voice="Google UK English Female" text={order.answer.join(' ')} />
                </div>
            </>}
        </div>
    );
}
export default Question;

const CheckButton = {
    play: {
        hover: { backgroundColor: '#f8f8ff' },
        button: {
            backgroundColor: '#12dc5533',
            borderColor: '#12dc55'
        }
    },
    resume: { button: {} },
    pause: { button: {} },
    stop: { button: {} }
};