import React, { useState } from 'react';

function Score({ onChange }) {
    const [score, setScore] = useState({ correct: 0, wrong: 0 });
    onChange.current = (value) => {
        if (value) { score.correct += 1; }
        else { score.wrong += 1; }
        setScore({ ...score });
    };
    if (score.correct > 0 || score.wrong > 0) {
        return (
            <>
                {score.correct > 0 && <div className="score">Correct <span className="correct">{score.correct}</span></div>}
                {score.wrong > 0 && <div className="score">Wrong <span className="wrong">{score.wrong}</span></div>}
            </>
        );
    }
    return null;
}
export default Score;